body {
    padding-top: 0;
    margin-top: 0;
}
.image-bg-fluid-height,
.image-bg-fixed-height {
    text-align: center;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    background-size: cover;
    -o-background-size: cover;
}

.image-bg-fluid-height {
    background: url('https://images.unsplash.com/photo-1444427169197-de497742b62d?ixlib=rb-0.3.5&q=80&fm=jpg&crop=entropy&w=1080&fit=max&s=11e4283bde12a12515ed7568a54d9deb') no-repeat top center fixed;
    background-size: cover;
    padding: 15em;
}

.image-bg-fixed-height {
    background: url('https://images.unsplash.com/photo-1444427169197-de497742b62d?ixlib=rb-0.3.5&q=80&fm=jpg&crop=entropy&w=1080&fit=max&s=11e4283bde12a12515ed7568a54d9deb') no-repeat top center fixed;
    height: 450px;
}

.img-center {
    margin: 0 auto;
}

section {
    padding: 75px 0;
}

.section-heading {
    margin: 30px 0;
    font-size: 4em;
}

.section-lead {
    margin: 30px 0;
}

.section-paragraph {
    margin: 30px 0;
}

footer {
    margin: 50px 0;
}

@media(max-width:768px) {
    section {
        padding-top: 25px;
        padding-bottom: 25px;
    }

    .section-heading {
        font-size: 2em;
    }
}